import React, { useEffect, useState } from 'react';
import {
  Badge,
  Button,
  Image,
  Input,
  message,
  Modal,
  Select,
  Space,
  Switch,
  Table,
  Tag,
} from 'antd';
import type { ColumnsType } from 'antd/es/table';
import jwtDecode from 'jwt-decode';
import {
  DeleteOutlined,
  ExclamationCircleOutlined,
  CheckOutlined,
  CloseOutlined,
  CloudUploadOutlined,
  EditOutlined,
  CloudDownloadOutlined,
} from '@ant-design/icons';
import { connect } from 'react-redux';
import { Dispatch, bindActionCreators } from 'redux';
import * as BlogActions from '@/redux/actionCreator';
import MyPagination from '@/components/pagination';
import dayjs from 'dayjs';
import './index.less';
import { statusPublish } from '@/utils/constants';
import { handleNotAdd, handleNotDelete, handleNotUpdate, onNotChangeStatus, onNotChangePublishStatus } from '@/utils/prompt';
const { confirm } = Modal;
const { Search } = Input;
interface DataType {
  isTop: boolean | undefined;
  introduction?: string;
  title?: string;
  cover: string | undefined;
  key: React.Key;
  _id: string;
  username?: string;
  avatar?: string;
  articleIds?: string[];
  tags: string[];
  views?: number;
  comment?: number;
  like?: number;
  collect?: number;
  publishStatus: number;
  createTime: string;
  updateTime: string;
}
interface ArticleData {
  msg: string;
  totalCount: number;
  page: number;
  pageSize: number;
  data: DataType[];
}
const ArticleList = (props: any) => {
  const token = jwtDecode(localStorage.getItem('token') as string) as object | any;
  const role_type = token[0].role[0].role_type
  const columns: ColumnsType<DataType> = [
    {
      title: 'æ–‡ç« æ ‡é¢˜',
      dataIndex: 'title',
      render: (_, record) => {
        return <p className='introduction' style={{ width: '8rem' }}>{record.title}</p>;
      },
    },
    {
      title: 'å°é¢',
      dataIndex: 'cover',
      render: (_, record) => {
        return <Image width={50} height={50} src={record.cover}></Image>;
      },
    },
    {
      title: 'ç®€ä»‹',
      dataIndex: 'introduction',
      render: (_, record) => {
        return <p className='introduction' style={{ width: '20rem' }}>{record.introduction}</p>;
      },
    },
    {
      title: 'åˆ†ç±»',
      dataIndex: 'categories',
    },
    {
      title: 'æ ‡ç­¾',
      dataIndex: 'tags',
      render: (_, record) => {
        let result = [];
        for (let i = 0; i < record.tags.length; i += 3) {
          result.push(record.tags.slice(i, i + 3)); // i=0 0-3 i=3 3-6
        }
        return result.map((item, index) => {
          return (
            <div style={{ marginBottom: 10 }} key={index}>
              {item.map((sub) => (
                <Tag style={{ marginRight: 10 }} key={sub}>
                  {sub}
                </Tag>
              ))}
            </div>
          );
        });
      },
    },
    {
      title: 'è®¿é—®é‡',
      dataIndex: 'views',
      align: 'center',
      render: (_, record) => {
        return <p style={{ width: '5rem' }}>{record.views}</p>;
      },
    },
    {
      title: 'è¯„è®º',
      dataIndex: 'comment',
      align: 'center',
      width: 100,
      render: (_, record) => {
        return <p style={{ width: '5rem' }}>{record.comment}</p>;
      },
    },
    {
      title: 'ç½®é¡¶',
      dataIndex: 'isTop',
      render: (_, record) => {
        return (
          <Switch
            checkedChildren={<CheckOutlined />}
            unCheckedChildren={<CloseOutlined />}
            checked={record.isTop}
            onChange={checked => role_type ? onNotChangeStatus() : onChangeStatus(checked, record)}
          />
        );
      },
    },
    {
      title: 'å‘å¸ƒçŠ¶æ€',
      dataIndex: 'publishStatus',
      render: (_, record) => {
        const published: any = {
          1: 'å·²å‘å¸ƒ',
          2: 'æœªå‘å¸ƒ',
        };
        const enums: any = {
          1: 'success',
          2: 'error',
        };
        return (
          <Space direction="vertical" style={{ width: '5rem' }}>
            <Badge status={enums[record.publishStatus]} text={published[record.publishStatus]} />
          </Space>
        );
      },
    },
    {
      title: 'åˆ›å»ºæ—¶é—´',
      dataIndex: 'createTime',
      render: time => {
        return dayjs(time * 1000).format('YYYY-MM-DD HH:mm:ss');
      },
    },
    {
      title: 'ä¿®æ”¹æ—¶é—´',
      dataIndex: 'updateTime',
      render: time => {
        return time && dayjs(time * 1000).format('YYYY-MM-DD HH:mm:ss');
      },
    },
    {
      title: 'æ“ä½œ',
      key: 'action',
      align: 'center',
      render: (_, record) => {
        return (
          <div>
            <Button
              type="primary"
              ghost
              shape="circle"
              onClick={() => {
                role_type ? onNotChangePublishStatus() : onChangePublishStatus(record);
              }}
              style={{ marginRight: '5px' }}
            >
              {record.publishStatus === 1 ? <CloudDownloadOutlined /> : <CloudUploadOutlined />}
            </Button>
            {record.publishStatus === 2 && (
              <>
                <Button
                  type="primary"
                  ghost
                  shape="circle"
                  icon={<EditOutlined />}
                  onClick={() => {
                    role_type ? handleNotUpdate() : props.history.push(`/admin/article/update/${record._id}`);
                  }}
                  style={{ marginRight: '5px' }}
                />
                <Button
                  type="primary"
                  danger
                  ghost
                  shape="circle"
                  icon={<DeleteOutlined />}
                  onClick={() => {
                    role_type ? handleNotDelete() : articleDelete(record);
                  }}
                  style={{ marginRight: '5px' }}
                />
              </>
            )}
          </div>
        );
      },
    },
  ];
  // æ–‡ç« åˆ—è¡¨
  const [list, setList] = useState<DataType[]>([]);
  // åˆ†é¡µæ€»æ•°
  const [total, setTotal] = useState(0);
  // å½“å‰ç¬¬å‡ é¡µ
  const [currentPage, setCurrentPage] = useState(1);
  // æ¯é¡µæ˜¾ç¤ºæ¡æ•°
  const [pageSize, setPageSize] = useState(10);
  // è·å–æ–‡ç« åˆ—è¡¨æ•°æ®
  useEffect(() => {
    props.BlogActions.asyncArticleListAction(currentPage, pageSize, '', 0, 0).then((res: ArticleData) => {
      // è·å–æ–‡ç« 
      let { data, totalCount, page, pageSize } = res.data as unknown as ArticleData;
      setList(data);
      setTotal(totalCount);
      setCurrentPage(page);
      setPageSize(pageSize);
    });
  }, [currentPage, pageSize, props.BlogActions]);

  // æ–°å¢æ–‡ç« 
  const handleArticleAdd = () => {
    props.history.push(`/admin/article/insert`);
  };
  // å‘å¸ƒ
  const onChangePublishStatus = (record: DataType) => {
    if (record.publishStatus === 2) {
      record.publishStatus = 1;
      // ä¿®æ”¹å‘å¸ƒçŠ¶æ€
      props.BlogActions.asyncArticlePublishStatusUpdateAction({
        publishStatus: record.publishStatus,
        id: record._id,
      }).then(() => {
        message.success('æ–‡ç« å‘å¸ƒæˆåŠŸ');
      });
    } else {
      record.publishStatus = 2;
      props.BlogActions.asyncArticlePublishStatusUpdateAction({
        publishStatus: record.publishStatus,
        id: record._id,
      }).then(() => {
        message.success('æ–‡ç« ä¸‹çº¿æˆåŠŸ');
      });
    }
  };

  // æ›´æ–°æ–‡ç« ç½®é¡¶çŠ¶æ€
  const onChangeStatus = (checked: boolean, row: DataType) => {
    row.isTop = checked
    setList([...list]);
    props.BlogActions.asyncArticleTopStatusUpdateAction({
      isTop: checked,
      id: row._id,
    }).then((res: ArticleData) => {
      message.success(res.msg)
    });
  };
  // åˆ é™¤æ–‡ç« 
  const articleDelete = (item: DataType) => {
    confirm({
      title: 'ä½ ç¡®å®šè¦åˆ é™¤å—?',
      icon: <ExclamationCircleOutlined />,
      onOk() {
        // å…ˆå°†è¦åˆ é™¤çš„æ•°æ®è¿‡æ»¤æ‰å†è°ƒç”¨æ¥å£
        setList(list.filter((it) => it._id !== item._id));
        message.success('æ–‡ç« åˆ é™¤æˆåŠŸ');
        props.BlogActions.asyncArticleDeleteAction(item._id).then(() => {
          props.BlogActions.asyncArticleListAction(currentPage, pageSize, '', 0, 0).then(
            (res: ArticleData) => {
              // è·å–æ–‡ç« 
              let { data, totalCount, page, pageSize } = res.data as unknown as ArticleData;
              setList(data);
              setTotal(totalCount);
              setCurrentPage(page);
              setPageSize(pageSize);
            }
          );
        });
      },
    });
  };
  // æœç´¢
  const onSearch = (value: string) => {
    props.BlogActions.asyncArticleListAction(currentPage, pageSize, value, 0, 0).then(
      (res: ArticleData) => {
        let { data, totalCount, page, pageSize } = res.data as unknown as ArticleData;
        setList(data);
        setTotal(totalCount);
        setCurrentPage(page);
        setPageSize(pageSize);
      }
    );
  };
  // å‘å¸ƒçŠ¶æ€æœç´¢
  const handleChange = (publishStatus: number) => {
    props.BlogActions.asyncArticleListAction(currentPage, pageSize, '', 0, publishStatus).then(
      (res: ArticleData) => {
        let { data, totalCount, page, pageSize } = res.data as unknown as ArticleData;
        setList(data);
        setTotal(totalCount);
        setCurrentPage(page);
        setPageSize(pageSize);
      }
    );
  };
  // è·³è½¬é¡µæ•°æ®æ˜¾ç¤º
  const onChangePage = (page: number, pageSize: number) => {
    // é‡æ–°è°ƒç”¨æ¥å£å°†å‚æ•°ä¼ é€’è¿‡å»
    props.BlogActions.asyncArticleListAction(page, pageSize, '', '', '').then((res: ArticleData) => {
      // è·å–åˆ—è¡¨æ•°æ®
      let { data } = res.data as unknown as ArticleData;
      setList(data);
      // åˆ‡æ¢è¡Œ
      setCurrentPage(page);
      // æ ¹æ®é¡µé¢æ•°æ®æ˜¾ç¤ºé¡µç 
      setPageSize(pageSize);
    });
  };
  return (
    <div>
      <div className="cate_title">
        <div>
          <Button type="primary" onClick={role_type ? handleNotAdd : handleArticleAdd} className="btn">
            æ–°å¢æ–‡ç« 
          </Button>
        </div>
        <div>
          <span>å‘å¸ƒçŠ¶æ€ï¼š</span>
          <Select
            defaultValue={0}
            style={{ width: 120 }}
            onChange={handleChange}
            options={statusPublish}
          ></Select>
          <Search
            className="search"
            allowClear
            placeholder="è¯·è¾“å…¥æ–‡ç« åç§°"
            onSearch={onSearch}
            enterButton
          />
        </div>
      </div>
      <Table
        columns={columns}
        dataSource={list}
        rowKey={(item) => {
          return item._id + Date.now();
        }}
        pagination={false}
      />
      <MyPagination
        pageSize={pageSize}
        currentPage={currentPage}
        total={total}
        onChange={onChangePage}
      ></MyPagination>
    </div>
  );
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    BlogActions: bindActionCreators(BlogActions, dispatch),
  };
};
export default connect(null, mapDispatchToProps)(ArticleList);
